.container {
  height: 100vh;
  width: 100vw;
  font-size: 28;
  color: #010101;
  padding-top: 20px;
  font-size: 24px;
}

.info_container {
  width: 100vw;
  display: flex;

  align-items: flex-start;
  padding: 20px;
  box-shadow: 0px 3px 3px rgba(0, 0, 0, 0.15);
}

.details_container {
  width: 100vw;
  display: flex;
  flex-direction: column;
  padding-left: 30px;
  padding-right: 100px;
}

.outlet_container {
  width: 100vw;
  display: flex;
  flex-direction: column;
  padding: 20px;
  box-shadow: 0px 3px 3px rgba(0, 0, 0, 0.15);
}

.image {
  object-fit: contain;
}

.p {
  margin: 0;
}

.h1 {
  margin-top: 0;
}

.link_back {
  margin-left: 20px;
  display: flex;
}

.outlet_link {
  line-height: 1.8;
}
